.ReasonViewBox {
  margin-top: 75px;
  display: grid;
  grid-template-columns: 75px auto 75px;
  grid-template-rows: 75vh 60px;
}
.ReasonViewText {
  grid-column-start: 2;
  grid-column-end: 3;
  width: 100%;
  height: 100%;
  border: 1px solid #888;
  padding-left: 30px;
  padding-top: 30px;
}

.ReasonViewBack {
  grid-row-start: 2;
  grid-row-end: 3;
  width: 400px;
  margin-top: 5px;
  margin-left: 75px;
}
.ReasonViewButton {
  grid-row-start: 2;
  grid-row-end: 3;
  width: 400px;
  margin-top: 5px;
  position: relative;
  left: 1355px;
}
